// src/pages/customer/OrderConfirmPage.jsx
import React, { useEffect, useMemo, useState } from "react";
import styled from "styled-components";
import { useParams, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Header from "../../components/common/Header.jsx";
import {
  selectCartTotalAmount,
  selectCartItems,
} from "../../store/cartSlice.js";
import { paths } from "../../routes/paths.js";
import { showErrorToast, showSuccessToast } from "../../utils/toast.js";
import { getBoothAccount, createOrder } from "../../api/customerApi.js";
import { addOrderId } from "../../store/orderIdsSlice.js";

export default function OrderConfirmPage() {
  const { boothId, tableId } = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const totalAmount = useSelector(selectCartTotalAmount);
  const cartItems = useSelector(selectCartItems);

  const [account, setAccount] = useState(null);
  const [accLoading, setAccLoading] = useState(true);
  const [accError, setAccError] = useState(null);

  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [agree, setAgree] = useState(true);
  const [submitting, setSubmitting] = useState(false);

  //   UI 
  const [copied, setCopied] = useState(false);
  const hideCopiedSoon = () => {
    setCopied(true);
    setTimeout(() => setCopied(false), 1200);
  };

  //  API
  useEffect(() => {
    let canceled = false;
    async function fetchAccount() {
      try {
        setAccLoading(true);
        const data = await getBoothAccount(Number(boothId));
        if (!canceled) {
          setAccount(data);
          setAccError(null);
        }
      } catch (e) {
        if (!canceled) {
          setAccount(null);
          setAccError("   .");
        }
        // eslint-disable-next-line no-console
        console.error(e);
      } finally {
        if (!canceled) setAccLoading(false);
      }
    }
    if (boothId) fetchAccount();
    return () => {
      canceled = true;
    };
  }, [boothId]);

  //  
  const cleanPhone = useMemo(() => phone.replace(/[^\d]/g, ""), [phone]);

  // (      /  )
  const tableNo = useMemo(() => {
    const s = sessionStorage.getItem("tableNo");
    const n = Number(s);
    return Number.isFinite(n) && n > 0 ? n : Number(tableId) || 1;
  }, [tableId]);

  //   ( + )
  const handleCopyAccount = async () => {
    try {
      if (!account?.bank || !account?.account) return;

      const text = `${account.bank} ${account.account}`;

      // Clipboard API 
      if (navigator?.clipboard?.writeText) {
        await navigator.clipboard.writeText(text);
      } else {
        // Fallback
        const ta = document.createElement("textarea");
        ta.value = text;
        ta.setAttribute("readonly", "");
        ta.style.position = "fixed";
        ta.style.top = "-9999px";
        document.body.appendChild(ta);
        ta.select();
        document.execCommand("copy");
        document.body.removeChild(ta);
      }

      showSuccessToast(" .");
      hideCopiedSoon();
    } catch (e) {
      // eslint-disable-next-line no-console
      console.error(e);
      showErrorToast(" .   .");
    }
  };

  const submit = async () => {
    if (!name.trim()) return showErrorToast(" .");
    if (!cleanPhone.trim()) return showErrorToast(" .");
    if (!agree) return showErrorToast("  .");
    if (!Array.isArray(cartItems) || cartItems.length === 0)
      return showErrorToast("  .");

    try {
      setSubmitting(true);

      // cart  API payload 
      const items = cartItems.map((it) => ({
        foodId: it.foodId,
        name: it.name,
        price: it.price,
        imageUrl: it.imageUrl || "",
        quantity: it.quantity,
      }));

      const payload = {
        boothId: Number(boothId),
        tableNo, //   URL   
        items,
        payment: {
          payerName: name.trim(),
          amount: Number(totalAmount) || 0,
        },
      };

      const res = await createOrder(payload);
      const orderId = res?.orderId;
      if (!orderId) {
        throw new Error("  .");
      }

      //      Redux orderId  (  )
      dispatch(
        addOrderId({ tableId: Number(tableId), orderId: Number(orderId) })
      );

      showSuccessToast(`${name}   .`);
      navigate(paths.pending(boothId, tableId, orderId));
    } catch (e) {
      // eslint-disable-next-line no-console
      console.error(e);
      showErrorToast("  .    .");
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Page>
      <Header
        title=""
        leftIcon={<span style={{ fontSize: 20 }}></span>}
        onLeft={() => navigate(-1)}
        rightIcon={<span />}
      />

      <Body>
        {/*   */}
        <Section>
          <H2></H2>
          <Row>
            <Label> </Label>
            <Value>{(Number(totalAmount) || 0).toLocaleString()}</Value>
          </Row>
        </Section>

        <Divider />

        {/*    */}
        <Section>
          <H2></H2>
          <Helper>     .</Helper>

          {accLoading ? (
            <Skeleton>   </Skeleton>
          ) : accError ? (
            <ErrorText>{accError}</ErrorText>
          ) : (
            <>
              <AccountGrid>
                <Col>
                  <Sub></Sub>
                  <Strong>{account?.bank || "-"}</Strong>
                </Col>

                <Col>
                  <Sub></Sub>
                  <AccountRow>
                    <AccountInline>
                      <Strong aria-label="">{account?.account || "-"}</Strong>

                      {/*      */}
                      <CopyBtn
                        type="button"
                        onClick={handleCopyAccount}
                        disabled={!account?.bank || !account?.account}
                        aria-label="  "
                        title=" "
                      >
                        
                      </CopyBtn>

                      {copied && <CopiedBadge></CopiedBadge>}
                    </AccountInline>
                  </AccountRow>
                </Col>

                <Col>
                  <Sub></Sub>
                  <Strong>{account?.accountHolder || "-"}</Strong>
                </Col>
              </AccountGrid>
            </>
          )}
        </Section>

        <Divider />

        {/*   */}
        <Section>
          <H3>  !</H3>

          <InputBox>
            <Input
              placeholder=" ."
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </InputBox>

          <InputBox>
            <Input
              placeholder="  "
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              inputMode="tel"
              pattern="[0-9]*"
            />
          </InputBox>

          <Agree type="button" onClick={() => setAgree(!agree)}>
            <Check $on={agree}>{agree ? "" : ""}</Check>
            <span>   </span>
          </Agree>
        </Section>
      </Body>

      <Bottom>
        <SubmitBtn onClick={submit} disabled={submitting || accLoading}>
          {submitting ? " " : ""}
        </SubmitBtn>
      </Bottom>
    </Page>
  );
}

/* ===== styled ===== */
