// src/pages/manager/MenuManagePage.jsx
import React, { useEffect, useMemo, useState } from "react";
import styled from "styled-components";
import AppLayout from "../../components/common/manager/AppLayout.jsx";
import { useParams } from "react-router-dom";

import MenuCard from "../../components/common/manager/menu/MenuCard.jsx";
import MenuEditorRow from "../../components/common/manager/menu/MenuEditorRow.jsx";
import {
  addMenu,
  updateMenu,
  deleteMenu,
  toggleMenuAvailable,
} from "../../api/manager/menuApi.js";
import { toURL, API_PUBLIC } from "../../api/api.js";

/**  fetch  */
async function handleResponse(res) {
  const contentType = res.headers.get("content-type") || "";
  const isJson = contentType.includes("application/json");
  if (!res.ok) {
    let detail;
    try {
      detail = isJson ? await res.json() : await res.text();
    } catch {
      detail = null;
    }
    const message =
      (detail && (detail.message || detail.error || JSON.stringify(detail))) ||
      `HTTP ${res.status} ${res.statusText}`;
    const err = new Error(message);
    err.status = res.status;
    err.detail = detail;
    throw err;
  }
  if (res.status === 204) return null;
  return isJson ? res.json() : res.text();
}

/**     */
async function fetchMenus(boothId) {
  const url = toURL(API_PUBLIC.LIST_MENUS_BY_BOOTH(boothId));
  const res = await fetch(url, {
    method: "GET",
    credentials: "include",
    headers: { Accept: "application/json" },
  });
  return handleResponse(res);
}

export default function MenuManagePage() {
  const { boothId } = useParams(); // ex) /manager/:boothId/menus
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);

  //  / 
  const [creating, setCreating] = useState(false);
  const [createDraft, setCreateDraft] = useState({
    boothId: boothId,
    name: "",
    price: "",
    description: "",
    previewImage: null,
    available: true,
    category: "FOOD", // : 
  });

  const [editingId, setEditingId] = useState(null);
  const [editDraft, setEditDraft] = useState({});

  const reload = async () => {
    setLoading(true);
    try {
      const list = await fetchMenus(boothId);
      setItems(Array.isArray(list) ? list : []);
    } catch (e) {
      alert(e.message || "  .");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (!boothId) return;
    reload();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boothId]);

  //    (  +  )
  const handleToggleAvailable = async (item, next) => {
    const id = item.menuItemId;
    const backup = items;
    setItems((prev) =>
      prev.map((it) => (it.menuItemId === id ? { ...it, available: next } : it))
    );
    try {
      await toggleMenuAvailable(id, next);
    } catch (e) {
      alert(e.message || "  .");
      setItems(backup); // 
    }
  };

  // 
  const handleDelete = async (item) => {
    if (!window.confirm(`'${item.name}' ?`)) return;
    const backup = items;
    setItems((prev) => prev.filter((it) => it.menuItemId !== item.menuItemId));
    try {
      await deleteMenu(boothId, item.menuItemId);
    } catch (e) {
      alert(e.message || " .");
      setItems(backup);
    }
  };

  //  
  const handleEdit = (item) => {
    setEditingId(item.menuItemId);
    setEditDraft({
      boothId: boothId,
      name: item.name ?? "",
      price: item.price ?? "",
      description: item.description ?? "",
      previewImage: item.previewImage ?? null,
      available: item.available ?? true,
      category: item.category ?? "FOOD", // : 
    });
  };

  //  
  const submitEdit = async () => {
    const id = editingId;
    if (!id) return;

    const payload = {
      boothId: boothId,
      name: editDraft.name?.trim(),
      price: Number(editDraft.price),
      description: editDraft.description?.trim(),
      previewImage: editDraft.previewImage ?? null,
      available: editDraft.available,
      category: editDraft.category ?? "FOOD", // : 
    };

    try {
      await updateMenu(boothId, id, payload);
      setEditingId(null);
      setEditDraft({});
      await reload();
    } catch (e) {
      alert(e.message || " .");
    }
  };

  // 
  const submitCreate = async () => {
    if (!createDraft.name?.trim()) return alert(" ");
    if (!createDraft.price) return alert(" ");

    try {
      await addMenu(boothId, {
        boothId: boothId,
        name: createDraft.name.trim(),
        price: Number(createDraft.price),
        description: createDraft.description?.trim(),
        available: true,
        previewImage: createDraft.previewImage ?? null,
        category: createDraft.category ?? "FOOD", // : 
      });
      setCreating(false);
      setCreateDraft({
        boothId: boothId,
        name: "",
        price: "",
        description: "",
        previewImage: null,
        available: true,
        category: "FOOD",
      });
      await reload();
    } catch (e) {
      alert(e.message || " .");
    }
  };

  const selling = useMemo(() => items.filter((i) => i.available), [items]);
  const stopped = useMemo(() => items.filter((i) => !i.available), [items]);

  return (
    <AppLayout title=" ">
      <HeaderLine>
        <AddBtn onClick={() => setCreating((v) => !v)}>+  </AddBtn>
      </HeaderLine>

      <Stack>
        {/*     */}
        {creating && (
          <MenuEditorRow
            mode="create"
            draft={createDraft}
            setDraft={setCreateDraft}
            onCancel={() => {
              setCreating(false);
              setCreateDraft({
                boothId: boothId,
                name: "",
                price: "",
                description: "",
                previewImage: null,
                available: true,
                category: "FOOD",
              });
            }}
            onSubmit={submitCreate}
          />
        )}

        <SectionTitle on> </SectionTitle>
        {loading && items.length === 0 ? (
          <div></div>
        ) : (
          selling.map((it) =>
            editingId === it.menuItemId ? (
              <MenuEditorRow
                key={it.menuItemId}
                mode="edit"
                draft={editDraft}
                setDraft={setEditDraft}
                onCancel={() => {
                  setEditingId(null);
                  setEditDraft({});
                }}
                onSubmit={submitEdit}
              />
            ) : (
              <MenuCard
                key={it.menuItemId}
                item={it}
                onEdit={handleEdit}
                onDelete={handleDelete}
                onToggleLocal={(id, available) =>
                  handleToggleAvailable(it, available)
                }
              />
            )
          )
        )}

        <SectionTitle> </SectionTitle>
        {stopped.map((it) =>
          editingId === it.menuItemId ? (
            <MenuEditorRow
              key={it.menuItemId}
              mode="edit"
              draft={editDraft}
              setDraft={setEditDraft}
              onCancel={() => {
                setEditingId(null);
                setEditDraft({});
              }}
              onSubmit={submitEdit}
            />
          ) : (
            <MenuCard
              key={it.menuItemId}
              item={it}
              onEdit={handleEdit}
              onDelete={handleDelete}
              onToggleLocal={(id, available) =>
                handleToggleAvailable(it, available)
              }
            />
          )
        )}
      </Stack>
    </AppLayout>
  );
}

const HeaderLine = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 14px;
`;

const AddBtn = styled.button`
  display: flex;
  align-items: center;
  gap: 8px;
  background: #ff6a2b;
  color: #fff;
  border: 0;
  border-radius: 10px;
  padding: 10px 14px;
  cursor: pointer;
  font-weight: 600;
  &:hover {
    filter: brightness(0.98);
  }
`;

const Stack = styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
`;

const SectionTitle = styled.div`
  margin: 14px 0 6px;
  color: #6b7280;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 8px;
  &::before {
    content: "";
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: ${({ on }) => (on ? "#ff6a2b" : "#9aa0a6")};
  }
`;
